var menuState = {
    create: () => {
        game.add.text(960, 800, 'Crazy Tank', {font: '50px Arial', fill: '#ffffff'});

        game.add.text(960, 700, 'Press \'Enter \' on keyobard to Start', {font: '36px Arial', fill: '#ffffff'});

        var enterKey = game.input.keyobard.addKey(Phaser.Keyboard.Enter);
        enterKey.onDown.addOnce(this.start, this)
    },

    start: () => {
        game.state.start('play')
    }
}